import React, { Component } from 'react';
import Typography from '@material-ui/core/Typography';
import Slider from '@material-ui/core/Slider';
import styled from 'styled-components';
import RecipeCard from '../components/RecipeCard';
import GetForm from '../components/getForm';

function valuetext(value) {
    return `${value}g carbs`;
}
export default class startPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            user: "",
            focus: "",
            show: false,
            maxCarbs: 30,
            maxCalories: 400,
            maxProtein: 30,
            maxFat: 30,
            recipes: [],
            likes: [],
            dislikes: []
        };
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    setMaxCalories = (event, newValue) => {
        this.setState({ maxCalories: newValue });
    }
    setMaxCarbs = (event, newValue) => {
        this.setState({ maxCarbs: newValue });
    }

    setMaxProtein = (event, newValue) => {
        this.setState({ maxProtein: newValue });
    }
    setMaxFat = (event, newValue) => {
        this.setState({ maxFat: newValue });
    }
    
    setSliders() {
        var focus = this.state.focus
        if (focus === "Endurance sports") {
            this.setState({ maxCalories: 700 });
            this.setState({ maxCarbs: 100 });
            this.setState({ maxProtein: 80 });
            this.setState({ maxFat: 20 });
        }
        else if (focus === "Power sports") {
            this.setState({ maxCalories: 600 });
            this.setState({ maxCarbs: 50 });
            this.setState({ maxProtein: 100 });
            this.setState({ maxFat: 40 });
        }
        else if (focus === "Keto") {
            this.setState({ maxCalories: 500 });
            this.setState({ maxCarbs: 10 });
            this.setState({ maxProtein: 10 });
            this.setState({ maxFat: 100 });
        }
        else if (focus === "Weight loss") {
            this.setState({ maxCalories: 100 });
            this.setState({ maxCarbs: 50 });
            this.setState({ maxProtein: 50 });
            this.setState({ maxFat: 50 });
        }
    }

    getRecipesApi() {
        var api_key = 'f92d5c6716cc42e3922e26d9bc1ad0b5';
        var url = 'https://api.spoonacular.com/recipes/findByNutrients?apiKey=';
        url += api_key;
        url += '&maxCalories=' + this.state.maxCalories
        url += '&maxCarbs=' + this.state.maxCarbs
        url += '&maxProtein=' + this.state.maxProtein
        url += '&maxFat=' + this.state.maxFat
        console.log(url);
        fetch(url)
            .then((response) => {
                return response.json();
            })
            .then((data) => {
                this.setState({
                    recipes: data
                });
            })
    }

    componentDidMount() {
        let name = getUserName();
        if (name !== null) {
            this.refreshUser(name)
        }
    }

    async refreshUser(value) {
        let type = localStorage.getItem(value);
        this.setState({ user: value });
        await this.setState({ focus: type })
        await this.setSliders()
        await this.getRecipesApi()
        this.setState({ show: true });

    }

    handleSubmit(event) {
        event.preventDefault();
        this.refreshUser()
    }

    render() {
        var data = this.state.recipes;
        var bool = true;
        var focus = this.state.focus
        let form = "";
        let h2 = "";
        let name = getUserName();
        let show = this.state.show;
        let recipes = "";
        if (show) {
            h2 = <h2>Hello {name}</h2>
            recipes = <div>
                <GetRecipes>
                    <h2>Recipes for your preference: {focus}</h2>
                    {data.length > 0 && <RecipeCard recipes={data} show={bool} />}
                    <div className="sliderbox">
                        <div id="slider">
                            <Typography id="discrete-slider" gutterBottom> Calories </Typography>
                            <Slider
                                defaultValue={this.state.maxCalories}
                                getAriaValueText={valuetext}
                                aria-labelledby="discrete-slider"
                                valueLabelDisplay="auto"
                                step={50}
                                marks={true}
                                min={50}
                                max={800}
                                onChange={this.setMaxCalories}
                            />
                        </div>

                        <div id="slider">
                            <Typography id="discrete-slider" gutterBottom>  Carbs (gram)</Typography>
                            <Slider id="slider"
                                defaultValue={this.state.maxCarbs}
                                getAriaValueText={valuetext}
                                aria-labelledby="discrete-slider"
                                valueLabelDisplay="auto"
                                step={10}
                                marks={true}
                                min={10}
                                max={100}
                                onChange={this.setMaxCarbs}
                            />
                        </div>

                        <div id="slider">
                            <Typography id="discrete-slider" gutterBottom>Fat (gram)</Typography>
                            <Slider id="slider"
                                defaultValue={this.state.maxFat}
                                getAriaValueText={valuetext}
                                aria-labelledby="discrete-slider"
                                valueLabelDisplay="auto"
                                step={10}
                                marks={true}
                                min={1}
                                max={100}
                                onChange={this.setMaxFat}
                            />
                        </div>

                        <div id="slider">
                            <Typography id="discrete-slider" gutterBottom> Protein (gram)</Typography>
                            <Slider id="slider"
                                defaultValue={this.state.maxProtein}
                                getAriaValueText={valuetext}
                                aria-labelledby="discrete-slider"
                                valueLabelDisplay="auto"
                                step={10}
                                marks={true}
                                min={10}
                                max={100}
                                onChange={this.setMaxProtein}
                            />
                        </div>
                        <button onClick={this.handleSubmit}>Apply filters</button>
                    </div>
                </GetRecipes>
            </div>
        } else {
            form =
                <div className="container">
                    <h2>Fill in your personal data and preferences</h2>
                    <GetForm name={true} />
                </div>
        }
        return (
            <div>
                {h2}
                {form}
                {recipes}
            </div>
        );
    }
}

const GetRecipes = styled.div`
	background-color: #685369;
  color: white
  padding: 2em;
`;

function getUserName() {
    let name = localStorage.getItem("user");
    if (name !== null) {
        return name;
    }
    return null;
}