import { Nav } from 'rsuite';
import { Navbar } from 'rsuite';
import React, { Component } from 'react';
import { Button } from '@material-ui/core';
import { Link } from "react-router-dom";
import image from "../images/logo.png"
import '../App.css';

export default class Navigation extends Component {
    render() {
        return (
            <Navbar>
                <Navbar.Body id="navbox">
                    <Nav id="nav">
                    <Nav.Item>
                        <Link to="/">
                                <Button>
                                    <img className="logo" src={image} alt="logo"/>
                                </Button>
                            </Link>
                        </Nav.Item>
                        <Nav.Item>
                        <Link to="/">
                                <Button>
                                    Home
                                </Button>
                            </Link>
                        </Nav.Item>
                        <Nav.Item>      
                            <Link to="/likes">
                                <Button>
                                    Likes
                                </Button>
                            </Link>
                        </Nav.Item>
                        <Nav.Item>
                        <Link to="/preferences">
                                <Button>
                                    Preferences
                                </Button>
                            </Link>
                        </Nav.Item>
                    </Nav>
                </Navbar.Body>
            </Navbar>
        )
    }
}