import React, { Component } from 'react';

export default class preferences extends Component {
    constructor(props) {
        super(props);
        this.state = {
            userName: "",
            type_rec: ""
        };
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    componentDidMount() {
        let user = getUserName();
        this.setState({ userName: user });
    }
    handleInputChange(event) {
        const target = event.target;
        const value = target.select
        const name = target.name;

        this.setState({
            [name]: value
        });
    }
    handleSubmit(event) {
            if (this.state.type_rec) {
                localStorage.setItem(this.state.user, this.state.type_rec);
            }
            event.preventDefault()
            window.location.href = "/"
    }

    render() {
        let user = this.state.userName;
        let test = ""
        if (user) {
            test =
                <div>
                    <h2>
                        Change your preferences:
                    </h2>
                    <form name="preferences" onSubmit={this.handleSubmit}>
                        <label>
                            <select type="select" name="type_rec" value={this.state.type_rec} onChange={this.handleInputChange}>
                                <option value="Endurance sports" selected>Endurance sports</option>
                                <option value="Power sports">Power sports</option>
                                <option value="Keto">Keto</option>
                                <option value="Weight loss">Weight loss</option>
                                <option value="Nothing">Nothing specifics</option>
                            </select>
                        </label>
                        <input type="submit" value="Submit" />
                    </form>
                </div>
        }
        else {
            test =
                <div>
                    <h2>
                        Fill in your preferences on home screen
                    </h2>
                </div>
        }
        return (
            <div>
                {test}
            </div>
        );
    }
}
function getUserName() {
    let name = localStorage.getItem("user");
    if (name !== null) {
        return name;
    }
    return null;
}