import React, { Component } from 'react';
import Title from './Title';
import Image from './Image';
import styled from 'styled-components';
import ThumbUpIcon from '@material-ui/icons/ThumbUp';
import ThumbDownIcon from '@material-ui/icons/ThumbDown';
import IconButton from '@material-ui/core/IconButton';
import { Link } from "react-router-dom";
import { Button } from '@material-ui/core';

export default class RecipeCard extends Component {
    render() {
        return (
            <div id="recipes">
                {this.props.recipes.map((item, index) => {
                    return (
                        <Card key={item.title}>
                            <div>
                                <Link to={"/recipes/" + item.id + "/" + item.title}>
                                    <Button>
                                        <Image source={item.image} text={item.title} />
                                    </Button>
                                </Link>
                                <Title title={item.title} />
                                <h3>Per serving: </h3>
                                <ul>
                                    <li>calories: {item.calories}</li>
                                    <li>carbs: {item.carbs}</li>
                                    <li>fat: {item.fat}</li>
                                    <li>protein: {item.protein}</li>
                                </ul>
                                {this.props.show === true &&
                                    <div>
                                        <IconButton id={item.title} aria-label="like recipe" onClick={() => setLikes(item)}>
                                            <ThumbUpIcon />
                                        </IconButton>
                                        <IconButton aria-label="dislike recipe" onClick={() => setDislike(item)}>
                                            <ThumbDownIcon />
                                        </IconButton>
                                    </div>
                                }
                            </div>
                        </Card>

                    )
                })}
            </div>
        )
    }
}
function setLikes(item) {
    if (localStorage.getItem(item.id + "dislike") != null) {
        localStorage.removeItem(item.id + "dislike");
    }
    localStorage.setItem(item.id + "like", JSON.stringify(item))
}

function setDislike(item) {
    if (localStorage.getItem(item.id + "like") != null) {
        localStorage.removeItem(item.id + "like");
    }
    localStorage.setItem(item.id + "dislike", JSON.stringify(item))
}

const Card = styled.div`
	max-width: 350px;
	width: 100%;
	display: inline-block;
	border-radius: 3px;
	text-decoration: none;
	background-color: #EFA48B;
    margin: 0 10px 25px;
    box-shadow: 7px 7px 50px -10px rgba(0, 0, 0, .5);
`;