import React, { Component } from 'react';



export default class getForm extends Component {
    constructor(props) {
        super(props);
        this.state = {
            user: "",
            type_rec: "",
        }
        this.handleInputChange = this.handleInputChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    componentDidMount() {
        let name = localStorage.getItem("user");
        if (name !== null) {
            let type = localStorage.getItem(name);
            this.setState({ user: name });
            this.setState({ type_rec: type })
        }
    }
    handleInputChange(event) {
        const target = event.target;
        const value = target.type === 'select' ? target.select : target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });
    }

    handleSubmit(event) {
        if (this.props.name) {
            if (this.state.user && this.state.type_rec) {
                localStorage.setItem("user", this.state.user);
                localStorage.setItem(this.state.user, this.state.type_rec);
            }
            else if (!this.state.user && this.state.type_rec) {
                alert('Name cannot be emty');
            }
            else if (!this.state.type_rec && this.state.user) {
                localStorage.setItem("user", this.state.user);
                localStorage.setItem(this.state.user, "Endurance sports");
                this.setState({ type_rec: "Endurance sports" })
            }
            else {
                alert('Fill in your name and choose where you want to focus on')
            }
        } else {
            if (this.state.type_rec) {
                localStorage.setItem(this.state.user, this.state.type_rec);
                
               this.props.history.push('/');
            }
        }
    }
    render() {
        let bool = this.props.name;
        let name = "";
        if (bool) {
            name =
                <label>
                    Name:
                     <input type="text" name="user" value={this.state.user} onChange={this.handleInputChange} />
                </label>
        }
        return (
            <form name="preferences" onSubmit={this.handleSubmit}>
                {name}
                <label>
                <select type="select" name="type_rec" value={this.state.type_rec} onChange={this.handleInputChange}>
                        <option value="Endurance sports" selected>Endurance sports</option>
                        <option value="Power sports">Power sports</option>
                        <option value="Keto">Keto</option>
                        <option value="Weight loss">Weight loss</option>
                        <option value="Nothing">Nothing specifics</option>
                    </select>
                </label>
                <input type="submit" value="Submit" />
            </form>
        )
    }
}