import '../App.css';
import React, { Component } from 'react';

export default class steps extends Component {
    constructor(props) {
        super(props);
        this.state = {
            name: this.props.match.params.name,
            steps: [],
            ingredients: [],
            img: "",
            nutrition: new Map()
        }
    }

    componentDidMount() {
        var api_key = 'f92d5c6716cc42e3922e26d9bc1ad0b5';
        var url = 'https://api.spoonacular.com/recipes/';
        url += this.props.match.params.id

        var ing_url = url
        var img = url
        var nutrition = url

        ing_url += "/ingredientWidget.json?apiKey="
        ing_url += api_key;

        img += "/information?apiKey="
        img += api_key;

        nutrition += "/nutritionWidget.json?apiKey="
        nutrition += api_key;



        fetch(ing_url)
            .then((response) => {
                return response.json();
            })
            .then((data) => {
                this.setState({
                    ingredients: data.ingredients
                })
            })

        fetch(img)
            .then((response) => {
                return response.json();
            })

            .then((data) => {
                this.setState({
                    img: data.image
                })
                this.setState({
                    steps: data.analyzedInstructions[0].steps
                })
            })

        fetch(nutrition)
            .then((response) => {
                return response.json();
            })
            .then((data) => {
                var map = new Map()
                map['Calories'] = data.calories
                map['Carbs'] = data.carbs
                map['Fat'] = data.fat
                map['Protein'] = data.protein
                this.setState({
                    nutrition: map
                })
            })
    }

    render() {
        var steps = this.state.steps
        var ing = this.state.ingredients
        var image = this.state.img
        var nutrition = this.state.nutrition
        return (
            <div>
                <h1>Steps from recipe {this.state.name}</h1>
                <img id="img" src={image} alt={image} />
                <h3>Ingredients: </h3>
                <ul className="ing">
                    {ing.map((item) => {
                        return (
                            <li>{item.amount.metric.value} {item.amount.metric.unit} {item.name}</li>
                        )
                    })}
                </ul>
                <ul className="ing">
                    {Object.keys(nutrition).map((key, item) => {
                        console.log(key + " " + item)
                        return (
                            <li>{key}: {nutrition[key]}</li>
                        )
                    })}
                </ul>
                <section className="steps">
                    {steps.map((item, index) => {
                        return (
                            <p> <h4>step {index + 1}:</h4> {item.step}</p>
                        )
                    })}
                </section>
            </div>)
    }
}

