import React, { Component } from 'react';
import styled from 'styled-components';

export default class Title extends Component {
    render() {
        return (
            <Header>
                {this.props.title}
            </Header>
        )
    }
}

const Header = styled.h3`
    text-align: center;
    margin: 5px;
    font-family: monospace;
    font-size: 24px;
    line-height: 32px;
    `;